*{
  font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
.form-bg{
  
  width:50%;
  
  
}

@media screen and (max-width:576px){
  .form-bg{
    display:flex;
  }
}

.form-text1{
 
  border:1px solid rgb(118, 108, 108);
  width:200px;
  display:flex;
  gap:1px;
  padding:2px;

}
.form-icon{
  font-size: 15px;
  color:rgb(34, 233, 8);

}



.input-form{
  border:none;
  background:none;
}
.input-form:focus{
  outline: none;
}

.distance{
  width:400px;
  border:2px solid rgb(19, 17, 17);
  display:flex;
  justify-content: space-between;
  padding:10px;

}

.distance-head{
  font-size:20px;
  font-weight: 500;
}

.distance-value{
  font-size:25px;
  color:rgb(68, 6, 126);
  font-family:sans-serif;
  

}
.origin-icon{
  border:2px solid #000;
  border-radius:10px;
}
.button-calc{
  display:flex;
  align-self:flex-end;
  width:460px;
  
}
@media screen and (max-width:576px){
  .button-calc{
    display:none;
  }
}

.button-calc1{
  color:aliceblue;
  
}

@media screen and (min-width:576px){
  .button-calc1{
    display:none
  }
}

.form-location{
  padding:10px;
 display:flex;
 flex-direction:column;
 gap:10px;
}


.map-size{
  width:100%;
  height:100%;
}
.map-form-container{
  background-color:aliceblue;
  display:flex;
  height:100vh;
  
  
}
@media screen and (max-width:576px) {
  .map-form-container{
    display:flex;
    flex-direction: column-reverse;
    padding:10px;
    


  }

  
}

.company-logo{
  height:50px;
}

.map-para{
 
  color:blueviolet;
  text-align:center;
  font-family:  sans-serif;
}

@media screen and(max-width:576px){
  .map-para{
    display: none;
  }
}

.mapsize{
  height:100px;
}

